* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
}
:root {
  --time: 0.4s;
  --dark: #4a4a4a;
  --light-dark: #9e9b9b;
  --grey: #c0b7bd;
  --white: #f1f7f8;
  --white-hover: #ece8e8;
  --light-white: #cbc9ce;
  --theme: #4608ad;
  --theme-opaque: #4608ad;
  --theme-hover: #661eda;
  --nav-theme: #543a7e;
}
html {
  scroll-behavior: smooth;
}
body {
  background: url("./assets/background.jpg");
  /* background-color: #c5c4c3; */
  background-attachment: fixed;
  background-size: cover;
  /* background: var(--purple); */
  /* background: radial-gradient(circle at center, var(--yellow), var(--theme)); */
  /* background-color: var(--yellow); */
  /* background-color: #cdec1b; */
}
.burger-description {
  display: none;
  position: absolute;
  left: 40vw;
  border-radius: 0.5px;
  z-index: 5;
  cursor: pointer;
  height: 25px;
}
.burger-content {
  position: relative;
  top: 10px;
  left: 0;
  min-width: 30px;
  height: 2px;
  background-color: var(--white);
  border-radius: 0.5px;
  transition: background-color var(--time) ease-in,
    transform var(--time) ease-in;
}
.burger-content.burger-open {
  transform: translateX(-5px);
  height: 2px;
  background-color: transparent;
  border-radius: 0.5px;
  transition: background-color var(--time) ease-in,
    transform var(--time) ease-in;
}
.burger-content::before {
  content: "";
  position: absolute;
  width: 100%;
  height: inherit;
  background-color: var(--white);
  border-radius: inherit;
  transform: translateY(-10px);
  transition: transform var(--time) ease-in;
}
.burger-content.burger-open::before {
  height: inherit;
  background-color: var(--white);
  border-radius: inherit;
  transform: translateX(5px) rotate(45deg);
  transition: transform var(--time) ease-in;
}
.burger-content::after {
  content: "";
  position: absolute;
  width: 100%;
  height: inherit;
  background-color: var(--white);
  transform: translateY(10px);
  border-radius: inherit;
  transition: transform var(--time) ease-in;
}
.burger-content.burger-open::after {
  background-color: var(--white);
  border-radius: inherit;
  transform: translateX(5px) rotate(-45deg);
  transition: transform var(--time) ease-in;
}
.nav-icons {
  padding: 0.5rem;
  position: relative;
}
nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  /* background-color: var(--theme); */
  /* position: sticky;
  top: 0;
  z-index: 1; */
  height: 8vh;

  /* border: 2px solid cyan; */
}
.nav-links a {
  /* font-family: "Roboto Condensed", sans-serif; */
  display: flex;
  flex-direction: column;
  /* border: 2px solid orange; */
  color: inherit;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  font-size: 0.9rem;
  justify-content: space-evenly;
  padding: 0.8rem;
  transition: color 0.2s ease-in-out;
}
.nav-links {
  display: flex;
  justify-content: center;
  align-items: center;
  /* margin-right: 2%; */
  /* border: 2px solid chartreuse; */
  color: var(--nav-theme);
}
.nav-links.nav-links-down {
  display: flex;
  width: 50%;
  pointer-events: auto;
  transition: right 0.3s ease-in-out;
  right: 0;
}
/* .nav-links-div {
  width: 100%;
  border: 5px solid purple;
  width: 50%;
} */
/* .nav ul i {
  font-size: 25px;
} */
.active-navlinks {
  position: relative;
  color: var(--white) !important;
}

.active-navlinks:after {
  content: "";
  background-color: var(--white);
  height: 2px;
  width: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
}
.nav-links a:hover {
  color: var(--white-hover);
  cursor: pointer;
  transition: color 0.2s ease-in-out;
}
.text-container {
  padding: 0;
  display: block;
  min-width: 50%;
  align-items: center;
  margin: 5vh auto;
  display: flex;
  place-items: center;
  justify-content: center;
  text-transform: capitalize;
  border-radius: 15px;
}
#typing-text {
  font-size: 2rem;
  color: var(--theme);
  white-space: nowrap;
}
#typing-text:before {
  content: "I am a ";
  color: var(--dark);
}
#typing-text:after {
  content: "";
  margin: 5px;
  z-index: -1;
  border: 2px solid var(--theme);
  animation: blink-Fade 0.3s ease-in infinite alternate;
}
@keyframes blink-Fade {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.youtube a {
  /* color: #c4302b; */
  color: white;
  text-decoration: none;
  margin: 20px;
}

