iframe {
  display: grid;
  place-items: center;
  background-color: transparent;
  width: 95vw;
  height: 70vh;
  margin: 10vh auto;
}
footer {
  width: 100%;
  background-color: var(--theme);
  font-size: 0.9rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 7px;
  color: #eeebeb;
}

@media (max-width: 992px) {
  .text-container {
    width: 90%;
  }
  #typing-text {
    font-size: 2rem;
  }
  footer {
    font-size: 1rem;
  }
}
@media (max-width: 768px) {
  .text-container {
    width: 90%;
  }
  .nav-links a {
    font-size: 1rem;
  }
  footer {
    font-size: 1rem;
  }
  nav {
    justify-content: space-around;
  }
}
@media screen and (max-width: 576px) {
  body {
    overflow-x: hidden;
  }
  .modal {
    transition: all 0.5s ease-in;
  }
  .modal .overlay {
    position: absolute;
    height: 100vh;
    width: 100vw;
    background-color: #303031a8;
    left: 0;
    top: 0;
    transition: all 0.5s ease-in;
  }
  .nav-icons:first-child {
    display: flex;
    height: 50px;
    align-items: center;
  }
  /* .nav-links-div {
    width: 0%;
    position: absolute;
    right: 0;
    top: 0;
    height: 100vh;
    overflow: hidden;
    z-index: -1;
  } */
  .nav-links-collapse {
    width: 50%;
  }
  .nav-links {
    position: absolute;
    background-color: var(--theme-opaque);
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    /* border: 2px solid #ff362f; */
    padding-top: 10vh;
    width: 50%;
    height: 100vh;
    top: 0;
    right: -50%;
    margin: 0;
    overflow: hidden;
    transition: right 0.3s ease-in-out;
    z-index: 1;
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
  }
  .nav-links:before {
    content: "";
    position: absolute;
    background: inherit;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    box-shadow: inset 0px 0px 2000px #ffffff93;
    z-index: -1;
    margin: -20%;
    filter: blur(10px);
  }
  .active-navlinks:after {
    content: "";
    background-color: var(--white);
    height: 2px;
    width: 50%;
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
  }
  .nav-links ul {
    overflow: hidden;
    width: 100%;
    cursor: pointer;
    height: 3.5rem;
  }
  .nav-links ul a {
    overflow: hidden;
    height: 100%;
  }
  .text-container {
    width: auto;
    text-align: justify;
    overflow: hidden;
  }
  nav {
    align-items: center;
    justify-content: space-around;
    flex-direction: column;
  }
  .nav-links a {
    font-size: 16px;
  }
  .burger-description {
    display: block;
  }
  footer {
    font-size: 0.9rem;
  }
}
@media (max-width: 365px) {
  .text-container {
    width: auto;
    overflow: hidden;
  }
  .footer {
    font-size: 0.8rem;
  }
}
